//Button
@mixin buttons {
  font-family: Rosarivo;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  border: none;
  color: $primary;
}

//Spacing

@mixin containerSpacing {
  padding: $sp-xxl 0;
  display: flex;
  gap: $mn;
}

//Typography in order of sections

//Global

@mixin h3Title {
  font-weight: 300;
  font-family: Roboto;
}

//Nav
@mixin navText {
  font-size: 20px;
  font-weight: 300;
}

//Hero
@mixin heroTitle {
  font-family: Rosarivo;
  font-size: 64px;
  color: $secondary;
}
@mixin heroText {
  font-size: 24px;
  font-weight: 300;
  line-height: 1.5;
}

//Treat
@mixin sectionTitle {
  font-family: Rosarivo;
  font-size: 36px;
  color: $secondary;
}

@mixin treatsText {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.7;
}

//Counter
@mixin countText {
  font-size: 16px;
  font-family: Rosarivo;
}

//Catering
@mixin cateringText {
  font-size: 16px;
  font-weight: 300;
}
@mixin labelText {
  font-size: 16px;
  font-weight: 300;
}
@mixin inputText {
  font-size: 16px;
  font-weight: 300;
  color: $secondary;
}

//Footer
@mixin footerText {
  font-family: Rosarivo;
}
